/* eslint-disable no-console */

import {register} from 'register-service-worker';

const text: {
    cached: string,
    error: string,
    offline: string,
    ready: string,
    updated: string
} = {
    'cached': 'Obsah strnky bol uloen (zacacheovan) pre budce pouitie.',
    'error': 'Nastala chyba poas registrcie service workerov:',
    'offline': 'Internetov pripojenie nebolo njden. Aplikcia be v offline mde.',
    'ready': 'Aplikcia je natan z pamte (cache) pomocou service workera. \nPre podrobnejie informcie, navtv https://goo.gl/AFskqB',
    'updated': 'Nov obsah je dostupn; prosm obnov (refreshni) strnku.'
};

if (process.env.NODE_ENV === `production`)
{
    register(`${process.env.BASE_URL}service-worker.js`, {
        ready()
        {
            console.log(text.ready);
        },
        cached()
        {
            console.log(text.cached);
        },
        updated()
        {
            console.log(text.updated);
        },
        offline()
        {
            console.log(text.offline);
        },
        error(error)
        {
            console.error(text.error, error);
        }
    });
}
